import PropTypes from 'prop-types';
import React from 'react';
import { ContextMenuTrigger } from 'react-contextmenu';
import { findDOMNode } from 'react-dom';
import cn from 'classnames';
import getDate from 'date-fns/get_date/index';
import getMonth from 'date-fns/get_month/index';
import getYear from 'date-fns/get_year/index';
import Combokeys from 'combokeys';

import dates from './utils/dates';
import { segStyle } from './utils/eventLevels';
import { notify } from './utils/helpers';
import { elementType } from './utils/propTypes';
import { dateCellSelection, slotWidth, getCellAtX, pointInBox } from './utils/selection';
import Selection, { getBoundsForNode, isEvent } from './Selection';
import { RIGHT_CLICK_DAY_CELL } from './ContextMenuTypes';

const combokeys = new Combokeys(document.documentElement);

class BackgroundCells extends React.Component {
  static propTypes = {
    date: PropTypes.instanceOf(Date),
    cellWrapperComponent: elementType,
    container: PropTypes.func,
    selectable: PropTypes.oneOf([true, false, 'ignoreEvents']),
    longPressThreshold: PropTypes.number,

    onSelectSlot: PropTypes.func.isRequired,
    onSelectEnd: PropTypes.func,
    onSelectStart: PropTypes.func,

    range: PropTypes.arrayOf(PropTypes.instanceOf(Date)),
    rtl: PropTypes.bool,
    type: PropTypes.string,
  };

  constructor(props, context) {
    super(props, context);

    this.state = {
      selecting: false,
      selectedDate: new Date(),
    };
  }

  componentDidMount() {
    this.props.selectable && this._selectable();

    combokeys.bind('up', () => {
      this.onArrowNavigate(-7);
    });

    combokeys.bind('down', () => {
      this.onArrowNavigate(7);
    });

    combokeys.bind('left', () => {
      this.onArrowNavigate(-1);
    });

    combokeys.bind('right', () => {
      this.onArrowNavigate(1);
    });
  }

  componentWillUnmount() {
    this._teardownSelectable();
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.selectable && !this.props.selectable) this._selectable();

    if (!nextProps.selectable && this.props.selectable) this._teardownSelectable();
  }

  onArrowNavigate(days) {
    const { selectedDate } = this.state;
    const [day, month, year] = [
      getDate(selectedDate),
      getMonth(selectedDate),
      getYear(selectedDate),
    ];
    const newDate = new Date(year, month, day + days);

    const previousDateId = this.generateId(new Date(selectedDate));
    const newDateId = this.generateId(newDate);

    // this.renderDaySelection(previousDateId, false);
    // this.renderDaySelection(newDateId, true);

    this.setState({
      selecting: false,
      click: true,
      selectedDate: newDate,
      selectedCell: this.generateId(newDate)
    });
    // const nDate = moveDate('month', {
    //   action: navigate.ARROW,
    //   newDate
    // });
    // console.log(newDate);

    // this.props.onNavigate(navigate.ARROW, nDate);
    // notify(this.props.onNavigate, newDate);
  }

  renderDaySelection = (id, isCurrent) => {
    const element = document.getElementById(id);
    if (!element) return;
  
    if (isCurrent) {
      element.classList.add('rbc-selected-cell');
      element.classList.add('rbc-selected-cell-click');
    } else {
      // element.classList.remove('rbc-selected-cell');
      // element.classList.remove('rbc-selected-cell-click');
      document.querySelectorAll('.rbc-day-bg').forEach(el => {
        el.classList.remove('rbc-selected-cell');
        el.classList.remove('rbc-selected-cell-click');
      });
    }
  }

  generateId(date) {
    return [date.getMonth() + 1, date.getDate(), date.getFullYear()].join('-');
  }

  isSelectedCell(cellId) {
    const { selectedCell } = this.state;
    return selectedCell === cellId;
  }
  

  render() {
    let { range, cellWrapperComponent: Wrapper, date: currentDate } = this.props;
    let { selecting, startIdx, endIdx, click, selectedDate, selectedCell } = this.state;
    // console.log({ selectedDate });
    console.log('RENDERING CELLS ==============================================');

    return (
      <div className="rbc-row-bg">
        {range.map((date, index) => {
          const cellId = this.generateId(date);
          const isNavigatedCellId = this.isSelectedCell(cellId);
          console.log({ cellId, selectedCell, isNavigatedCellId, selecting, range, index });
          let selected = (isNavigatedCellId && !selecting) || (selecting && index >= startIdx && index <= endIdx);
          return (
            <Wrapper key={index} value={date} range={range}>
              <div
                className="rbc-day-bg-wrapper"
                style={{ ...segStyle(1, range.length), height: '100%' }}
              >
                <ContextMenuTrigger
                  collect={props => ({ ...props, date })}
                  holdToDisplay={-1}
                  id={RIGHT_CLICK_DAY_CELL}
                >
                  <div
                    id={cellId}
                    style={{ height: '100%' }}
                    className={cn('rbc-day-bg', {
                      'rbc-today': dates.isToday(date),
                      'rbc-selected-cell': selected,
                      'rbc-selected-cell-click': selected && click,
                      'rbc-off-range-bg': dates.month(currentDate) !== dates.month(date),
                    })}
                  />
                </ContextMenuTrigger>
              </div>
            </Wrapper>
          );
        })}
      </div>
    );
  }

  _selectable() {
    let node = findDOMNode(this);
    let selector = (this._selector = new Selection(this.props.container, {
      longPressThreshold: this.props.longPressThreshold,
    }));

    selector.on('selecting', box => {
      let { range, rtl } = this.props;

      let startIdx = -1;
      let endIdx = -1;

      if (!this.state.selecting) {
        notify(this.props.onSelectStart, [box]);
        this._initial = { x: box.x, y: box.y };
      }
      if (selector.isSelected(node)) {
        let nodeBox = getBoundsForNode(node);

        ({ startIdx, endIdx } = dateCellSelection(this._initial, nodeBox, box, range.length, rtl));
      }

      this.setState({
        startIdx,
        endIdx,
        selecting: true,
        click: false,
      });
    });

    selector.on('beforeSelect', box => {
      this.setState({ selecting: false, click: false });
      if (this.props.selectable !== 'ignoreEvents') return;
      return !isEvent(findDOMNode(this), box);
    });

    selector.on('rightclick', point => {
      this.setState({ selecting: false, click: false });
      let rowBox = getBoundsForNode(node);
      let { range, rtl } = this.props;

      if (pointInBox(rowBox, point)) {
        let width = slotWidth(getBoundsForNode(node), range.length);
        let currentCell = getCellAtX(rowBox, point.x, width, rtl, range.length);

        this._rightClickSlot({
          startIdx: currentCell,
          endIdx: currentCell,
          action: 'click',
        });
      }
      this._initial = {};
    });

    selector.on('click', point => {
      this.setState({ selecting: false, click: false });
      if (!isEvent(findDOMNode(this), point)) {
        let rowBox = getBoundsForNode(node);
        let { range, rtl } = this.props;

        if (pointInBox(rowBox, point)) {
          let width = slotWidth(getBoundsForNode(node), range.length);
          let currentCell = getCellAtX(rowBox, point.x, width, rtl, range.length);
          const cellId = node.childNodes.item(currentCell).querySelector('.rbc-day-bg').getAttribute('id');
          const date = new Date(cellId.replace(/[-]/g, '/'));
          // console.log(nodeId, date);

          this._selectSlot({
            cellId,
            selectedDate: date,
            startIdx: currentCell,
            endIdx: currentCell,
            action: 'click',
          });
        }
      }

      this._initial = {};
    });

    selector.on('select', () => {
      this._selectSlot({ ...this.state, action: 'select' });
      this._initial = {};
      notify(this.props.onSelectEnd, [this.state]);
    });
  }

  _teardownSelectable() {
    if (!this._selector) return;
    this._selector.teardown();
    this._selector = null;
  }

  _selectSlot({ endIdx, startIdx, action, selectedDate, cellId }) {
    if (endIdx !== -1 && startIdx !== -1 && this.props.onSelectSlot) {
      this.props.onSelectSlot({
        start: startIdx,
        end: endIdx,
        action,
      });
      const dateState = selectedDate ? { selectedDate, selectedCell: cellId  } : null;
      this.setState({
        ...dateState,
        startIdx,
        endIdx,
        selecting: true,
        click: action === 'click',
      });
    }
  }

  _rightClickSlot({ endIdx, startIdx, action }) {
    if (endIdx !== -1 && startIdx !== -1 && this.props.onRightClickSlot) {
      this.props.onRightClickSlot({
        start: startIdx,
        end: endIdx,
        action,
      });
      this.setState({
        startIdx,
        endIdx,
        selecting: true,
        click: action === 'click',
      });
    }
  }
}

export default BackgroundCells;



